import echarts from 'echarts'

export default class draw_bar_chart {
  // 
  constructor(container) {
    this.container = `${container}`
    this.myBar = null
    this.bartitle = '1'
    this.legend = {
        data: ['1'],
        orient: 'vertical',
        right: 0,
        bottom: 40,
        top: 0,
        left: 0
      },
      this.series = [{
        name: '1',
        type: 'bar',
        data: [10, 23, 21, 34, 23]
      }]
    this.xAxis = {
      data: ['1', '2', '3', '4', '5'],
      name: '',
      nameLocation: 'end',
      axisLabel: {
        // showMinLabel: true,
        showMaxLabel: true
      },
      nameTextStyle: {
        align: 'center',
        padding: [10, 0, 0, 30],
      }
    }
    this.yAxis = {
      name: '',
      nameTextStyle: {
        align: 'center',
        padding: [0, 0, -5, 0], //s
        width: 5,
        // height: '100',
        overflow: 'breakAll',
        ellipsis: 'truncate'
      }
    }
    this.grid = {
      top: '25%',
      left: '10%',
      right: '28%',
      bottom: '12%'
    }
  }
  // 
  drawBar() {
    this.myBar = echarts.init(document.getElementById(this.container))

    let option = {
      tooltip: {
        trigger: 'axis',
        axisPointer: { // 
          type: 'shadow' // 'line' | 'shadow'
        }
      },
      title: {
        text: this.bartitle,
        textStyle: {
          fontSize: 15,
          fontFamily: ''
        }
      },
      legend: this.legend,
      grid: this.grid,
      xAxis: this.xAxis,
      yAxis: this.yAxis,
      series: this.series
    }
    this.myBar.setOption(option)
  } //
  initTitle(title) {
    this.bartitle = title
    this.drawBar()
  }
  //
  //
  setLegend(data = {
    orient: 'horizontal',
    right: 0,
    bottom: 0,
    top: 25,
    left: 80
  }) {
    this.legend = data
  }
  initlegend(data) {
    this.legend.data = data
  }
  legendO(data) {
    this.legend.orient = data
  }
  legendR(data) {
    this.legend.right = data
  }
  legendT(data) {
    this.legend.top = data
  }
  legendL(data) {
    this.legend.left = data
  }
  legendB(data) {
    this.legend.bottom = data
  }
  initxdata(data) {
    this.xAxis.data = data
  }
  initydata(data) {
    this.yAxis.data = data
  }
  initseries(data = {
    data: [],
    name: ''
  }) {
    data.type = 'bar'
    this.series = []
    this.series.push(data)
  }
  //
  addSeries(data = {
    data: [],
    name: ''
  }) {
    data.type = 'bar'
    this.series.push(data)
  }
  //grid
  setGrid(data = {
    top: '25%',
    left: '12%',
    right: '12%',
    bottom: '8%'
  }) {
    this.grid = data
  }
}



